/* Off-canvas menu (sidebar_first) */
.offcanvas-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 80vw;
  max-width: 360px;
  height: 100vh;
  background: #fff;
  box-shadow: -4px 0 12px rgba(0, 0, 0, 0.2);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 1001;
  overflow-y: auto;
  padding: 1rem;
}

/* Menu slides in from the right */
.offcanvas-menu.is-active {
  transform: translateX(0);
}

/* Overlay that appears behind the menu */
.site-overlay {
  display: none;
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  transition: opacity 0.3s ease-in-out;
  opacity: 0;
}

/* When overlay is active */
.site-overlay.is-active {
  display: block;
  opacity: 1;
}

/* Optional: prevent background scroll when menu is open */
body.menu-open {
  overflow: hidden;
}

/* ************************************************************** */
body {
  margin: 0;
  background-color: #fffff5;
}

body * {
  box-sizing: border-box; /* padding and margin won't effect the width */
}

h1,
h2,
h3,
h4,
h5,
p,
ul,
ol,
li {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5 {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans", sans-serif;
}
h1 strong,
h1 b,
h2 strong,
h2 b,
h3 strong,
h3 b,
h4 strong,
h4 b,
h5 strong,
h5 b {
  font-weight: 900;
}

h2 {
  font-size: 4em;
}

a:link,
a:visited {
  color: #c9a764;
}

.btn a,
a.btn {
  color: #ffffff;
  height: 3.75em;
  padding: 0.5em 3em;
  background-color: #000000;
  border-radius: 2em;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  align-self: start;
  font-family: "Open Sans", sans-serif;
  font-size: 1.1em;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  text-decoration: none;
}
.btn a:hover,
a.btn:hover {
  opacity: 0.9;
}

/********************/
/* site breakpoints */
/********************/
/* handheld */
@media all and (max-width: 767px) {
  html {
    font-size: 2.6vw;
  }
  .sitew {
    width: 100%;
  }
  .pagew {
    width: 100%;
  }
  /* /handheld */
}
/* desktop - small - 768 - 1024 */
@media all and (min-width: 768px) {
  html {
    font-size: 8.8px; /* 100% */
  }
  .sitew {
    width: 768px;
  }
  .pagew {
    width: 1365px;
  }
  /* /small */
}
/* desktop - medium - 1024 - 1366 */
@media all and (min-width: 1024px) {
  html {
    font-size: 11.7333333333px; /* 53.333333333333336% */
  }
  .sitew {
    width: 1024px;
  }
  /* /medium */
}
/* desktop -large - 1366+ */
@media all and (min-width: 1365px) {
  html {
    font-size: 15.640625px; /* 71.09375% */
  }
  .sitew {
    width: 1365px;
  }
  /* /large */
}
/* desktop - max - 1920+ */
@media all and (min-width: 1920px) {
  html {
    font-size: 22px; /* 100% - all media query body font-size math starts here */
  }
  .sitew {
    width: 1920px;
  }
  /* /max */
}
/********************/
/* /site breakpoints */
/********************/
.layout-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

/* main content regions */
main,
.breadcrumb,
footer {
  width: 100vw;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  display: flex;
  justify-content: center;
}

.header-content,
.main-content,
.footer-content {
  padding-left: 1.568em;
  padding-right: 1.568em;
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  /* border: 1px solid red; */
}
@media all and (min-width: 768px) {
  .header-content,
  .main-content,
  .footer-content {
    flex-basis: 1365px;
    padding-left: 7.568em;
    padding-right: 7.568em;
  }
}

/* /main content regions */
/* start- header contents */
header {
  background-color: #fffff5;
  z-index: 5;
  height: 8em;
  /* sticky */
  position: sticky;
  top: 0;
  overflow-y: visible;
  width: 100vw !important;
  display: flex;
  justify-content: center;
  border: 1px solid red;
  /* /sticky */
}
@media all and (min-width: 768px) {
  header {
    height: 15em;
  }
}
header .header-content {
  margin: 0 auto;
  flex-direction: row;
  max-width: 1365px;
  justify-content: space-between;
}
header .header-content .block-superfishmain {
  align-content: center;
  display: none;
}
header .header-content .block-superfishmain ul.menu {
  display: flex;
  gap: 1em;
  margin: 0;
}
header .header-content .block-superfishmain ul.menu li {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans", sans-serif;
  font-weight: 500;
  letter-spacing: 0.05em;
}
header .header-content .block-superfishmain ul.menu li strong,
header .header-content .block-superfishmain ul.menu li b {
  font-weight: 900;
}
header .header-content .block-superfishmain ul.menu li a,
header .header-content .block-superfishmain ul.menu li a:visited {
  text-decoration: none;
  color: black;
  text-transform: uppercase;
}
@media all and (min-width: 768px) {
  header .header-content .block-superfishmain {
    display: block;
  }
}
header .header-content .navburger {
  display: flex;
  justify-content: center;
  justify-self: flex-end;
  /* /navburger */
}
@media all and (min-width: 768px) {
  header .header-content .navburger {
    display: none;
  }
}
header .header-content .navburger button.menu-btn {
  /* remove button style */
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  /* set size */
  font-size: 2.5em;
}

/* /end - header contents */
/* start - breadcrumb contents */
.breadcrumb {
  display: flex;
  justify-content: center;
}

.breadcrumb-content {
  display: flex;
  justify-content: start;
  flex-grow: 1;
}

/* /end - breadcrumb contents */
/* start - main contents */
main {
  display: flex;
  justify-content: center;
}

/* /end - main contents */
/* start - footer contents */
footer {
  background-color: #c512e1;
}

/* /end - footer contents */
img {
  max-width: 100% !important;
  height: auto;
}

/* basic defaults for theme styling */
a#main-content {
  /* anchor link to main content has no height */
  height: 0px;
}

/**
* Responsive tables.
*/
@media all and (max-width: 767px) {
  /* handheld */
  th.priority-low,
  td.priority-low,
  th.priority-medium,
  td.priority-medium {
    display: none;
  }
}
@media all and (max-width: 1023px) and (min-width: 768px) {
  /* small 768px - 1024p */
  th.priority-low,
  td.priority-low {
    display: none;
  }
}
/* top-level blocks layout, fill view-width and include classes */
.node__content .layout .layout__region--content .block {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}
.node__content .layout .layout__region--content .block .block-content {
  margin-right: auto;
  margin-left: auto;
  max-width: 100%;
  padding-left: 1.568em;
  padding-right: 1.568em;
}
@media all and (min-width: 768px) {
  .node__content .layout .layout__region--content .block .block-content {
    max-width: 1365px;
    padding-left: 7.568em;
    padding-right: 7.568em;
  }
}
.node__content .layout .layout__region--content .block.block-width-full {
  width: 100vw;
  padding: 0;
  max-width: unset;
}
.node__content .layout .layout__region--content .block.block-width-full .block-content {
  margin-right: auto;
  margin-left: auto;
  max-width: unset;
}

.node__content .block.block-width-full > * {
  width: 100vw;
  max-width: unset;
}

/* /top-level blocks layout, fill view-width and include classes */
ul#uan_mainnavigation_2 li {
  border: 2px solid yellow;
}